import Vue from 'vue'
import App from './App.vue'
import Swiper from 'vue-awesome-swiper'

import router from './router'
import 'swiper/dist/css/swiper.css'
import VueResource from 'vue-resource';
Vue.use(VueResource)
Vue.use(Swiper)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App),
}).$mount('#app')
