<template>
  <div id="index">
    <div class="header">
      <i class="iconfont icon-sousuo sou"></i>
      <input type="text" placeholder="叫嚷着命运不公的人,其实还未真正触及命运。" />
      <dl class="sao">
        <dt>
          <i class="iconfont icon-xiangji"></i>
        </dt>
        <dd>发布</dd>
      </dl>
    </div>
    <div class="nav">
      <swiper :options="swiper_nav" class="na_list">
        <swiper-slide v-for="(i,index) in tit">
          <p @click="na_click(i.ext,index)" :key="index" :class="index == na?'na': ''">{{i.name}}</p>
        </swiper-slide>
      </swiper>
      <div class="geng">
        <i class="iconfont icon-gengduo"></i>
      </div>
    </div>
    <div class="main">
      <div v-for="i in big_news" class="news">
        <div class="bt">{{i.title}}</div>
        <div class="tu" v-if="i.multi_imgs.length">
          <div class="ku" v-for="m,x in tu_liang">
            <img :src="i.multi_imgs[x]" />
          </div>
        </div>
      </div>
    </div>
    <div class="footer">
      <ul class="fu">
        <li class="fa"><i class="iconfont icon-shouye"></i><p>首页</p></li>
        <li><i class="iconfont icon-bofang"></i><p>西瓜视频</p></li>
        <li><i class="iconfont icon-huo"></i><p>小视频</p></li>
        <li><i class="iconfont icon-iconfontzhizuobiaozhun08"></i><p>我的</p></li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "index",
  data() {
    return {
      news: "edu",
      big_news: [],
      swiper_nav: {
        slidesPerView: 6,
        freeMode: true
      },
      tit: [
        {
          name: "教育",
          ext: "edu"
        },
        {
          name: "星座",
          ext: "astro"
        },
        {
          name: "文化",
          ext: "cul"
        },
        {
          name: "房产",
          ext: "house"
        },
        {
          name: "体育",
          ext: "sports"
        },
        {
          name: "数码",
          ext: "tech"
        },
        {
          name: "游戏",
          ext: "games"
        },
        {
          name: "时政",
          ext: "finance"
        },
        {
          name: "娱乐",
          ext: "fashion"
        },
        {
          name: "汽车",
          ext: "auto"
        },
        {
          name: "美食",
          ext: "food"
        }
      ],
      tu_liang: 3,
      na: 0,
    };
  },
  created() {
    this.$http
      .jsonp(
        `https://pacaio.match.qq.com/irs/rcd?cid=56&ext=${this.news}&token=c786875b8e04da17b24ea5e332745e0f&num=20&expIds=20190106A13PFT%7C20190108A04MLS&page=0`,
        { callback: this.news }
      )
      .then(data => {
        this.big_news = data.data.data;
      });
  },
  beforeMount () {
    for(var i = 0;i < this.big_news.length;i++){
      if(this.big_news[i].multi_imgs.length <= 2){
        this.tu_liang = 2
      }else if(this.big_news[i].multi_imgs.length <= 1){
        this.tu_liang = 1
      }else{
        this.tu_liang = 0
      }
    };
  },
  methods: {
    na_click(i,m) {
      this.news = i;
      this.na = m
      this.$http
        .jsonp(
          `https://pacaio.match.qq.com/irs/rcd?cid=56&ext=${this.news}&token=c786875b8e04da17b24ea5e332745e0f&num=20&expIds=20190106A13PFT%7C20190108A04MLS&page=0`,
          { callback: this.news }
        )
        .then(data => {
          this.big_news = data.data.data;
        });
    }
  }
};
</script>

<style scoped>
.header {
  height: 14vw;
  background: #d33d3e;
  position: relative;
}
.header .sou {
  font-size: 3.8vw;
  position: absolute;
  left: 8.5vw;
  top: 5.5vw;
  color: #7a7a7a;
  z-index: 10;
}
.header input {
  height: 10.8vw;
  width: 79.4vw;
  border: none;
  position: absolute;
  left: 4vw;
  top: 2vw;
  padding-left: 10.2vw;
  box-sizing: border-box;
  font-size: 3.2vw;
  border-radius: 1vw;
}
.header .sao {
  position: absolute;
  right: 5vw;
  top: 2.6vw;
}
.header .sao i {
  color: #fff;
  font-size: 6.4vw;
}
.header .sao dd {
  font-size: 2.6vw;
  color: #ffe7ea;
  text-align: center;
}
.nav {
  height: 10vw;
  position: relative;
  border-bottom: 0.3vw solid #eee;
}
.nav .na_list {
  width: 86.8vw;
  height: 10vw;
  position: absolute;
  left: 0;
  text-align: center;
  line-height: 10vw;
}
.nav .geng {
  width: 13.2vw;
  height: 10vw;
  position: absolute;
  right: 0;
  text-align: center;
  font-size: 3.8vw;
  line-height: 10vw;
  border-left: 0.3vw solid #eee;
  box-sizing: border-box;
}
.na{
  color: #fa5a5a
}
.main{
  background: #f5f5f5;
  padding-bottom: 13.3vw;
  overflow: hidden;
}
.news{
  width:100vw;
  height: auto;
  margin-bottom: 2.66vw;
  padding: 0 4.6vw 4.4vw;
  background: #fff;
  box-sizing: border-box;
}
.news .bt{
  width: 91.46vw;
  padding: 4.4vw 0; 
  font-size: 4.6vw;
}
.news .tu{
  height: 19.33vw;
}
.news .tu .ku{
  width: 29vw;
  height: 19.33vw;
  margin: .5vw;
  float: left;
  text-align: center;
  overflow: hidden;
}
.news .tu img{
  height: 100%;
}
.footer{
  height: 13.3vw;
  background: #f4f5f9;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  border-top: #eee solid .3vw;
}
.footer .fu{
  display: flex;
  justify-content: space-around;
  text-align: center;
}
.footer .fu li{
  padding-top: 1.8vw;
}
.footer .fu li i{
  font-size: 5.8vw;
}
.footer .fu li p{
  font-size: 2.4vw;
}
.fa{
  color: #fa5a5a;
}
</style>